"use client";

import React from "react";
import CardSample from "@/components/custom-ui/card-sample";

export default function ProjectSection() {
    const cards = [
        {
            title: "3D PROJECTS",
            description:
                "High-quality 3D modeling, rigging, texturing, and animation for games, films, and virtual environments.",
            image: "/images/3d-project.png",
        },
        {
            title: "2D PROJECTS",
            description:
                "Creative concept art, illustration, and 2D animation for games, digital media, and promotional content.",
            image: "/images/2d-project.png",
        },
        {
            title: "CG PROJECTS",
            description:
                "Cinematic simulations and computer graphics to enhance visual storytelling in games and animation.",
            image: "/images/cg-project.png",
        },
        {
            title: "ANOTHER PROJECT",
            description: "Example of future projects added here.",
            image: "/images/cg-project.png",
        },
        // Add more cards if needed
    ];

    return (
        <section className="w-full py-20 bg-gradient-to-b from-emerald-50 to-white flex flex-col items-center">

            {/* TOP BAR: TITLE CENTERED + RETURN BUTTON ON RIGHT */}
            <div className="w-full max-w-[1300px] flex items-center justify-between px-4 mb-10">
                <div className="w-[90px]"></div>

                <h2 className="text-4xl font-bold text-center text-emerald-900 flex-1">
                    PROJECTS
                </h2>

                <a
                    href="/project"
                    className="
            px-6 py-2
            border-2 border-emerald-500
            text-emerald-900 font-semibold
            rounded-full
            transition-all duration-300
            hover:bg-emerald-500 hover:text-white
          "
                >
                    Return
                </a>
            </div>

            {/* GRID OF CARDS */}
            <div className="w-full max-w-[1300px] grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 px-4">
                {cards.map((card, index) => (
                    <div key={index} className="w-full h-64 sm:h-72 lg:h-64 xl:h-72">
                        <CardSample
                            title={card.title}
                            description={card.description}
                            image={card.image}
                        />
                    </div>
                ))}
            </div>

        </section>
    );
}
